
import { ref, reactive, onMounted, toRefs } from "vue"
export default {
  emits: ['skuClose'],
  props: {
    customBySlot: {
      type:Boolean,
      default:false
    }
  },
  setup(props:any,context:any) {
    // const customBySlot = ref(false)
    const data = reactive<any>({
      sku: [],
      goods: {},
    })

    onMounted(() => {
      fetch("https://storage.360buyimg.com/nutui/3x/data.js")
        .then((response) => response.json())
        .then((res) => {
          const { Sku, Goods, imagePathMap } = res
          data.sku = Sku
          data.goods = Goods
        }) //执行结果是 resolve就调用then方法
        .catch((err) => console.log("Oh, error", err)) //执行结果是 reject就调用catch方法
    })

    const skuClose = () => {
      context.emit("skuClose")
    }

    // 切换规格类目
    const selectSku = (ss: any) => {
      const { sku, skuIndex, parentSku, parentIndex } = ss
      if (sku.disable) return false
      data.sku[parentIndex].list.forEach((s:any) => {
        s.active = s.id == sku.id
      })
      data.goods = {
        skuId: sku.id,
        price: "6002.10",
        imagePath:
          "//img14.360buyimg.com/n4/jfs/t1/215845/12/3788/221990/618a5c4dEc71cb4c7/7bd6eb8d17830991.jpg",
      }
    }
    // 底部操作按钮触发
    const clickBtnOperate = (op: string) => {
      console.log("点击了操作按钮", op)
    }
    return { selectSku, clickBtnOperate,skuClose, ...toRefs(data) }
  },
}
